import * as d3 from "d3";
import * as chroma from "chroma-js";
import { CategoricalColors, UniformColorMaps, ColorBrewer } from "./COLORS";

export default class Color {
	constructor(metric = "time (inc)", colorMin = 0, colorMax = 0, colorMap = "OrRd", colorPoint = "9") {
		this.catColors = ["#3366cc", "#dc3912", "#ff9900", "#109618", "#990099", "#0099c6", "#dd4477", "#66aa00", "#b82e2e", "#316395", "#994499", "#22aa99", "#aaaa11", "#6633cc", "#e67300", "#8b0707", "#651067", "#329262", "#5574a6", "#3b3eac"];
		this.categoricalColors = CategoricalColors;
		this.colorbrewer = ColorBrewer;
		this.UniformColorMaps = UniformColorMaps;

		this.metric = metric;
		this.colorMin = colorMin;
		this.colorMax = colorMax;
		this.colors = this.colorbrewer[colorMap][colorPoint];
		this.colorPadding = this.setColorPadding(colorPoint);
		this.colorScale = this.setColorScale();

		this.grey = "#252525";
		this.highlight = "#AF9B90";

		if (metric == "MeanGradients") {
			this.target = "#4DAF4A";
			this.ensemble = "#C0C0C0";
			this.compare = "#043060";
		}
	}

	getDomain() {
		return this.colorScale.domain();
	}

	getColor(dictionary, attribute) {
		let value = dictionary[attribute];
		// TODO: Remove the attr_dict. 
		if(Object.keys(dictionary).includes("attr_dict")) {
			value = dictionary["attr_dict"][attribute];
		}
		return this.colorScale(value);
	}	

	getColorByValue(value) {
		return this.rgbArrayToHex(this.colorScale(value));
	}

	setColorPadding(colorPoint) {
		let ret = [];
		let fraction = 1 / colorPoint;
		for (let i = 0; i < colorPoint; i += 1) {
			ret.push(i * fraction);
		}
		ret.push(1);
		return ret;
	}

	setColorScale() {
		let colorScale = null;
		switch (this.metric) {
		case "Module":
			colorScale = d3.scaleOrdinal(d3.schemeCategory10);
			break;
		case "time (inc)":
			colorScale = chroma.scale(this.colors)
				.padding([0.0, 0.0])
				.gamma(0.5)
				.domain([this.colorMin, this.colorMax]);
			break;
		case "time":
			colorScale = chroma.scale(this.colors)
				.padding([0.0, 0.0])
				.gamma(0.5)
				.domain([this.colorMin, this.colorMax]);
			break;
		case "Imbalance":
			colorScale = chroma.scale(this.colors)
				.domain([0, 1]);
			break;
		case "MeanDiff": {
			let mmax = Math.max(Math.abs(this.colorMin), Math.abs(this.colorMax));
			colorScale = chroma.scale("RdYlGn")
				.padding([0.0, 0.0])
				.domain([mmax, -mmax]);
			break;
		}
		case "RankDiff":
			colorScale = chroma.scale(this.colors)
				.gamma(0.5)
				.domain([this.colorMin, this.colorMax]);
			break;
		case "MeanGradients":
			colorScale = chroma.scale(this.colors)
				.padding([0.0, 0.0])
				.gamma(0.5)
				.domain([this.colorMin, this.colorMax]);
			break;
		}
		return colorScale;
	}

	CYKToRGB(CMYK) {
		let result = {};
		let c = CMYK[0];
		let m = CMYK[1];
		let y = CMYK[2];
		let k = 0;

		result.r = 1 - Math.min(1, c * (1 - k) + k);
		result.g = 1 - Math.min(1, m * (1 - k) + k);
		result.b = 1 - Math.min(1, y * (1 - k) + k);

		result.r = Math.round(result.r * 255);
		result.g = Math.round(result.g * 255);
		result.b = Math.round(result.b * 255);

		function componentToHex(c) {
			var hex = c.toString(16);
			return hex.length == 1 ? "0" + hex : hex;
		}

		return "#" + componentToHex(result.r) + componentToHex(result.g) + componentToHex(result.b);
	}

	setContrast(hex) {
		const rgb = this.hexToRgb(hex);
		const o = Math.round(((parseInt(rgb.r) * 299) +
			(parseInt(rgb.g) * 587) +
			(parseInt(rgb.b) * 114)) / 1000);

		return (o > 128) ? "#000" : "#fff";
	}

	getAllColors() {
		return Object.keys(this.colorbrewer);
	}

	getCatColor(id) {
		return this.catColors[id];
	}

	hexToRgb(hex) {
		const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
		return result ? {
			r: parseInt(result[1], 16),
			g: parseInt(result[2], 16),
			b: parseInt(result[3], 16),
		} : null;
	}

	rgbToHex(r, g, b) {
		return "#" + ((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1);
	}

	rgbArrayToHex(color_arr) {
		color_arr = color_arr._rgb;
		let r = Math.floor(color_arr[0]);
		let g = Math.floor(color_arr[1]);
		let b = Math.floor(color_arr[2]);
		return "#" + ((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1);
	}

	valToPercentColor(val, colormap) {
		const idx = Math.min(
			Math.max(0, Math.round(val * colormap.length)),
			colormap.length - 1);

		return colormap[idx];
	}

	percentColToD3Rgb(percentCol) {
		const col256 = percentCol.map(elm => Math.round(elm * 255));
		return `rgb(${col256[0]}, ${col256[1]}, ${col256[2]})`;
	}

	valToD3Rgb(val, colormap) {
		return this.percentColToD3Rgb(this.valToPercentColor(val, colormap));
	}
}